._c-picture-with-copy
   background-color: $blanco
   padding-bottom: 50px
   @include maxMobile
      padding: 30px 0 0



   .o-container
      @include maxMobile
         padding: 0
         width: 100%   
   .o-grid-row
      @include mobileToPortrait
         flex-flow: column-reverse
      @include maxMobile
         flex-flow: column-reverse
      .col
         @include mobileToPortrait
            width: 100%
         @include maxMobile
            width: 100%
            padding: 0
   .m-b-100
      @include tabletPortraitToDesktopSmall
         margin-block: 25px
      @include mobileToPortrait
         margin-bottom: 50px
      @include maxMobile
         margin-bottom: 50px

   .grid-buttons
      a
         margin-bottom: 15px
   
   &--picture
      position: relative
      left: -10%
      @include maxMobile
         left: 0
         margin: 30px 0
      img
         width: 100%
         @include mobileToPortrait
            height: auto
      @include tabletPortraitToDesktopSmall
         left: 0
      @include mobileToPortrait
         min-height: 0
         height: 200px
         display: flex
         align-items: flex-start
         overflow: hidden
      @include maxMobile
         min-height: 0
   &--copy
      width: 100%
      position: relative
      @include square( absolute, 100px, -115px, -55px, $green-pastel )
      @include tabletPortraitToDesktopSmall
         padding: 0 15px
      @include mobileToPortrait
         overflow: hidden
      @include maxMobile
         padding: 0 30px
      h2
         max-width: 500px
         @include minDesktopSmall
            font-size: 2.5rem
         @include tabletPortraitToDesktopSmall
            font-size: 1.9rem
      h3
         font-family: $tipografia-principal
         margin: 40px 0 25px
         @include minDesktopSmall
            font-size: 1.4rem
      p
         line-height: 1.5
         @include tabletPortraitToDesktopSmall
            font-size: 1.2rem
         @include minDesktopSmall
            font-size: 1.15rem
         strong
            font-size: inherit
   
   &--footer
      position: relative
      left: 45%
      width: 50%
      @include tabletPortraitToDesktopSmall
         margin-top: 30px
      .col
         padding: 0 0
         position: relative
         margin-left: -7.5px
         @include square( absolute, 100px, 0, -100px, $dark-green )


   &--discover
      width: 100%
      max-width: 525px
      margin-left: auto
      @include tabletPortraitToDesktopSmall
         padding-left: 80px
      @include phabletToTablet
         margin: 0      
         max-width: 100%
      @include maxPhablet
         margin: 0
         max-width: 100%
      @include maxMobile
         padding: 0 20px   
      h3
         @include tabletPortraitToDesktopSmall
            font-size: 1.3rem
         @include minDesktopSmall
            font-size: 1.6rem
      p
         margin-top: 25px
         line-height: 1.5
         @include tabletPortraitToDesktopSmall
            font-size: 1.2rem
         @include minDesktopSmall
            font-size: 1.15rem
      img
         width: auto
         max-width: 100%
         display: block
         margin: 50px auto 0



   // For prepa
   &.custom-structure
      padding: 100px 0
      @include phabletToDesktopSmall
         padding: 30px 0 0
      @include maxMobile
         padding: 30px 0 0
      
      &.accent-prepa
         .o-grid-row
            &:nth-child(odd)
               ._c-picture-with-copy--picture
                  @include minDesktopSmall         
                     @include squareInAfter( absolute, 105px, 100%, 105px, $color-prepa )
                     @include squareInBefore( absolute, 105px, calc( 100% - 105px ), 0, $color-prepa-2 )
            &:nth-child(even)
               ._c-picture-with-copy--picture
                  @include minDesktopSmall
                     @include squareInBefore( absolute, 105px, -105px, 100%, $color-prepa  )                                    
      &.accent-profesional
         .o-grid-row
            &:nth-child(odd)
               ._c-picture-with-copy--picture
                  @include minDesktopSmall         
                     @include squareInAfter( absolute, 105px, 100%, 105px, $color-profesional-2 )
                     @include squareInBefore( absolute, 105px, calc( 100% - 105px ), 0, $color-profesional )
            &:nth-child(even)
               ._c-picture-with-copy--picture
                  @include minDesktopSmall
                     @include squareInBefore( absolute, 105px, -105px, 100%, $darker-green  )                                    
      .o-grid-row
         &:nth-child(even)
            ._c-picture-with-copy--copy
               margin-left: 100px
               @include tabletPortraitToDesktopSmall
                  margin-left: 0
               @include mobileToPortrait
                  margin-left: 0
               @include maxMobile
                  margin-left: 0               

      ._c-picture-with-copy--picture
         left: 0
         min-height: 410px
         display: flex
         justify-content: center
         position: relative
         @include tabletPortraitToDesktopSmall
            min-height: 0
         @include mobileToPortrait
            min-height: 0
            margin-top: 50px
            align-items: center
         @include maxMobile
            min-height: 0
            margin-top: 50px
         img
            min-height: 100%
            width: 100%
            max-width: unset
            @include mobileToPortrait
               height: auto
      
      ._c-picture-with-copy--copy
         max-width: 485px
         @include mobileToPortrait
            max-width: 100%
         h2
            margin-top: 50px
            @include tabletPortraitToDesktopSmall
               margin-top: 0
            @include mobileToPortrait
               margin-top: 0
            @include maxPhablet
               margin-top: 0
         p
            margin-bottom: 20px
            &:last-child
               margin-bottom: 0
         
      
   // For product
   // - prepa
   &.extended-info
      margin: 30px 0
      @include minDesktopSmall
         margin: 100px 0
      ._c-picture-with-copy--copy
         @include minDesktopSmall
            padding-left: 50px
         @include squareID( first, absolute, 105px, -105px, 0, $dark-green )
         h2
            margin-bottom: 40px
         p
            margin-top: 20px
         ul
            padding-left: 55px
            @include maxMobile
               padding-left: 40px
               padding-right: 10px
            li
               margin-bottom: 30px
               position: relative
               &::before
                  content: ""
                  position: absolute
                  width: 30px
                  height: 30px
                  background-position: center center
                  background-repeat: no-repeat
                  left: -50px   
                  @include maxMobile
                     left: -40px
                     transform: scale(.7)
               &:last-child
                  margin-bottom: 0
               span
                  display: block

               


      ._c-picture-with-copy--picture
         left: 0
         @include mobileToPortrait
            margin: 30px 0
      ._c-picture-with-copy--footer
         left: 0
         width: 100%
         .col
            padding: 0 10px
            margin: 0
         .square
            right: -90px
            @include desktopSmallToBig
               display: none
            @media (min-width: 1200px) and ( max-width: 1300px )
               display: none               
            @include phabletToTablet
               display: none
            @include maxPhablet
               right: 0
               width: 50px
               height: 50px
               top: 0px
               right: 10%
         img
            display: block
            margin-left: auto
            max-width: 100%
            &:first-child
               @include phabletToTablet
                  display: none
            @include maxPhablet
               margin: 20px auto 

      &.accent-prepa
         
         ._c-picture-with-copy--discover
            @media (min-width: 1300px)
               margin-top: -25%
            h3
               color: $dark-green

         ._c-picture-with-copy--copy
            @include squareID( second, absolute, 105px, 0, -105px, $color-prepa )

            .square
               @include maxMobile
                  display: none

            h2
               color: $dark-green
         
         ._c-picture-with-copy--footer
            .square
               background-color: $color-prepa
               @include tabletPortraitToDesktopSmall
                  width: 70px
                  height: 70px
                  right: -60px               
            

      &.accent-profeaso
         
         ._c-picture-with-copy--discover
            @media (min-width: 1300px)
               margin-top: -25%
            h3
               color: $dark-green

         ._c-picture-with-copy--copy
            .first
               background-color: $color-profesional-aso-3
            @include squareID( second, absolute, 105px, 0, -105px, $color-profesional-aso )

            .square
               @include maxMobile
                  display: none

            h2
               color: $dark-green
            
            ul
               li
                  &::before
                     background-image: url('../images/red-arrow.png')
                     background-size: 100%
         
         ._c-picture-with-copy--footer
            display: flex
            @include square( relative, 105px, 0, 0, $color-profesional-aso-2 )
            @include maxMobile
               display: none
            

      &.accent-profesemestral
         
         ._c-picture-with-copy--discover
            @media (min-width: 1300px)
               margin-top: -25%
            h3
               color: $dark-green

         ._c-picture-with-copy--copy
            .first
               background-color: $dark-green
            @include squareID( second, absolute, 105px, 0, -105px, #A6D342 )

            .square
               @include maxMobile
                  display: none

            h2
               color: $dark-green
            
            ul
               li
                  &::before
                     background-image: url('../images/arrow-green-pastel.png')
                     background-size: 100%
         
         ._c-picture-with-copy--footer
            display: flex
            @include square( relative, 105px, 0, 0, #A6D342 )
            @include maxMobile
               display: none
            
      